import React from 'react'
import FormField from '../FormField/FormField'

/**
 * `FormText` lets users enter a text for submission via a form.
 *
 * > **Note:** `<FormText />` renders a `<FormField />` with most props spread on.
 * For more details, see the [FormField](./formfield) component.
 *
 * The `type` of the input is over-written to `text`.
 *
 * It comes with accessible form features:
 *
 * - label and description
 * - validation messaging
 * - aria attributes
 *
 * **Requires *custom-event* polyfill for custom validations to work in IE11** [See Polyfills](./polyfills)
 */

const FormText = props => <FormField {...props} type="text" />

export default FormText
